.container {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 100vh;
}

.slider {
  position: relative;

  width: 200px;
}

.slider__track,
.slider__range,
.slider__left-value,
.slider__right-value {
  position: absolute;
}

.slider__track,
.slider__range {
  height: 5px;

  border-radius: 3px;
}

.slider__track {
  z-index: 1;

  width: 100%;

  background-color: #ced4da;
}

.slider__range {
  z-index: 2;

  background-color: #9fe5e1;
}

.slider__left-value,
.slider__right-value {
  margin-top: 20px;

  font-size: 12px;
  color: #dee2e6;
}

.slider__left-value {
  left: 6px;
}

.slider__right-value {
  right: -4px;
}

/* Removing the default appearance */
.thumb,
.thumb::-webkit-slider-thumb {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
}

.thumb {
  pointer-events: none;

  position: absolute;

  width: 200px;
  height: 0;

  outline: none;
}

.thumb--zindex-3 {
  z-index: 3;
}

.thumb--zindex-4 {
  z-index: 4;
}

.thumb--zindex-5 {
  z-index: 5;
}

/* For Chrome browsers */
.thumb::-webkit-slider-thumb {
  pointer-events: all;
  cursor: pointer;

  position: relative;

  width: 18px;
  height: 18px;
  margin-top: 4px;

  background-color: #f1f5f7;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #ced4da;
}

/* For Firefox browsers */
.thumb::-moz-range-thumb {
  pointer-events: all;
  cursor: pointer;

  position: relative;

  width: 18px;
  height: 18px;
  margin-top: 4px;

  background-color: #f1f5f7;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #ced4da;
}
